import React, {useCallback, useRef, useState} from "react";
import img2017 from "../../assets/images/2017.jpg";
import img2019 from "../../assets/images/2019.jpg";
import img2020 from "../../assets/images/2020.jpg";
import img2021 from "../../assets/images/2021.jpg";
import img2022 from "../../assets/images/2022.jpg";
import img2023 from "../../assets/images/2023.jpg";
import {
  ProfileListContainer,
  ProfileListTopic,
  ProfileContentContainer,
  ProfileListDesc,
  ProfileListImg,
  ProfileListItem,
  ProfileListTitle,
  ProfileListYear,
} from "../../styles/profile_styled";

const ProfileListArea = () => {
  const ProfileContent = [
    {
      year: "2017",
      img: img2017,
      title: " . ",
      desc:
        "    ,    .  .\n" +
        "     ,   . 1    ,     2018 4 2 .",
    },
    {
      year: "2019",
      img: img2019,
      title: "",
      desc:
        " K-55a1 . 606      .\n" +
        "    ,      ,     .",
    },
    {
      year: "2020",
      img: img2020,
      title: " .  ",
      desc:
        "          . \n" +
        "        ,         .",
    },
    {
      year: "2021",
      img: img2021,
      title: " .  ",
      desc:
        "1     ,       .\n" +
        "1     ,        ,      ,  .\n" +
        "            12      2022 .    .",
    },
    {
      year: "2022",
      img:img2022,
      title: "  ",
      desc:
        "                   .\n" +
        " 4        ,       ,              .        ." +
        "       ,        .",
    },
    {
      year: "2023",
      img: img2023,
      title: "  ",
      desc:
        "        ,    .   react     ,          .\n" +
        "      ,               .\n" +
        "3   2       .   ,       , 3     .",
    },
  ];

  const containerRef = useRef(null);

  const[isDragging, setIsDragging] = useState(false);

  const [startX, setStartX] = useState(0);

  const [totalX, setTotalX] = useState(0);

  const preventUnexpectedEffects = useCallback((e: MouseEvent) =>{
    e.preventDefault()
    e.stopPropagation()
  },[]);
  const onDragStart = (e: MouseEvent) => {
    preventUnexpectedEffects(e)
    setIsDragging(true);
    const x = e.clientX;
    setStartX(x);
    if(containerRef.current && 'scrollLeft' in containerRef.current){
      setTotalX(x + containerRef.current.scrollLeft);
    }
  };
  const onDragMove= (e: MouseEvent) => {
    preventUnexpectedEffects(e)
    if(!isDragging) return;

    const scrollLeft = totalX - e.clientX;

    if(containerRef.current && 'scrollLeft' in containerRef.current) {
      containerRef.current.scrollLeft = scrollLeft;
    }
  };
  const onDragEnd = (e: MouseEvent) => {
    if(!isDragging) return;
    if (!containerRef.current) return;

    setIsDragging(false);

    const endX = e.clientX;
    const childNodes = [...(containerRef.current?.childNodes ||[])];
    const dragDiff = Math.abs(startX - endX)
    if (dragDiff > 10) {
      childNodes.forEach((child) => {
        child.addEventListener('click', preventUnexpectedEffects);
      });
    } else {
      childNodes.forEach((child) => {
        child.removeEventListener('click', preventUnexpectedEffects);
      });
    }
  };
  return (
    <ProfileListContainer>
      <ProfileListTopic> </ProfileListTopic>
      <ProfileContentContainer
          ref={containerRef}
          onMouseDown={onDragStart}
          onMouseMove={onDragMove}
          onMouseUp={onDragEnd}
          onMouseLeave={onDragEnd}
      >
        {ProfileContent.map((profilelist) => (
          <ProfileListItem>
            <ProfileListYear>{profilelist.year}</ProfileListYear>
            <ProfileListImg src={profilelist.img} alt={profilelist.year} />
            <ProfileListTitle>{profilelist.title}</ProfileListTitle>
            <ProfileListDesc>{profilelist.desc}</ProfileListDesc>
          </ProfileListItem>
        ))}
      </ProfileContentContainer>
    </ProfileListContainer>
  );
};

export default ProfileListArea;
